var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"zval"},[_c('div',{staticStyle:{"height":"auto"}},[_c('div',{staticClass:"box"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-12"},[_c('span',[_c('img',{staticClass:"img-fluid kopidunia img-header",attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(5).jpg"),"alt":"Sejarah"}}),_c('div',{staticClass:"text-center content-header"},[_c('h1',{staticClass:"title"},[_vm._v("Sejarah Kopi")]),_c('p',{staticClass:"subtext"},[_vm._v(" Blog by "),_c('a',{attrs:{"href":""}},[_vm._v("William Wijaksana")])])]),_c('div',{staticClass:"text-justify content-body"},[_c('p',[_vm._v(" Sejarah kopi telah dicatat sejauh pada abad ke-9. Pertama kali, kopi hanya ada di Ethiopia, di mana biji-bijian asli ditanam oleh orang Ethiopia dataran tinggi. Akan tetapi, ketika bangsa Arab mulai meluaskan perdagangannya, biji kopi pun telah meluas sampai ke Afrika Utara dan biji kopi di sana ditanam secara massal. Dari Afrika Utara itulah biji kopi mulai meluas dari Asia sampai pasaran Eropa dan ketenarannya sebagai minuman mulai menyebar. ")]),_c('p',{staticClass:"subtext"},[_vm._v(" Blog by "),_c('a',{attrs:{"href":""}},[_vm._v("Ricky Hansen")])])]),_c('div',{staticClass:"text-justify content-body"},[_c('p',[_vm._v(" Sejarah kopi telah dicatat sejauh pada abad ke-9. Pertama kali, kopi hanya ada di Ethiopia, di mana biji-bijian asli ditanam oleh orang Ethiopia dataran tinggi. Akan tetapi, ketika bangsa Arab mulai meluaskan perdagangannya, biji kopi pun telah meluas sampai ke Afrika Utara dan biji kopi di sana ditanam secara massal. Dari Afrika Utara itulah biji kopi mulai meluas dari Asia sampai pasaran Eropa dan ketenarannya sebagai minuman mulai menyebar. "),_c('br'),_vm._v("Sejarah kopi sangat erat kaitannya dengan peradaban kaum muslim era kekhalifahan. Peradaban muslim punya pengaruh yang besar bagi perkembangan peradaban dunia, baik dalam hal sains, teknologi, budaya, seni, sastra, hingga kuliner. Budaya minum kopi adalah salah satunya. ")]),_c('br'),_c('img',{staticClass:"rounded float-left spasigambar",staticStyle:{"height":"300px","margin-right":"20px"},attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(6).jpg"),"alt":"Sejarah kopi"}}),_c('h4',[_c('b',[_vm._v("1. Budaya Minum Kopi Orang Muslim")])]),_c('p',[_vm._v(" Konon, tanaman kopi pertama kali ditemukan di daratan Afrika, tepatnya di daerah yang merupakan bagian dari negara Ethiopia, yaitu Abyssinia. Masyarakat Ethiopia mulai mengonsumsinya sejak abad ke-9. Pada saat itu kopi belum dikenal luas di dunia. Biji kopi menjadi komersial setelah dibawa oleh para pedagang Arab ke Yaman pada pertengahan abad ke-15. Kopi dipopulerkan menjadi minuman oleh orang-orang muslim. Istilah kopi juga lahir dari bahasa Arab, qahwah yang berarti kekuatan. >>>>>>> 21765b946434a5d71c61d44523bea33e4010c6d5 "),_c('br'),_vm._v("Sejarah kopi sangat erat kaitannya dengan peradaban kaum muslim era kekhalifahan. Peradaban muslim punya pengaruh yang besar bagi perkembangan peradaban dunia, baik dalam hal sains, teknologi, budaya, seni, sastra, hingga kuliner. Budaya minum kopi adalah salah satunya. ")]),_c('br'),_c('img',{staticClass:"rounded float-left spasigambar",attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(6).jpg"),"alt":"Sejarah kopi"}}),_c('h4',[_c('b',[_vm._v("1. Budaya Minum Kopi Orang Muslim")])]),_c('p',[_vm._v(" Konon, tanaman kopi pertama kali ditemukan di daratan Afrika, tepatnya di daerah yang merupakan bagian dari negara Ethiopia, yaitu Abyssinia. Masyarakat Ethiopia mulai mengonsumsinya sejak abad ke-9. Pada saat itu kopi belum dikenal luas di dunia. Biji kopi menjadi komersial setelah dibawa oleh para pedagang Arab ke Yaman pada pertengahan abad ke-15. Kopi dipopulerkan menjadi minuman oleh orang-orang muslim. Istilah kopi juga lahir dari bahasa Arab, qahwah yang berarti kekuatan. "),_c('br'),_c('br'),_vm._v("Berkat peradabannya yang lebih maju dari Afrika, Arab membudidayakan kopi sendiri dan mengekspornya ke penjuru dunia. Orang-orang Islam mulai menyebarluaskan kopi melalui Pelabuhan Mocha, Yaman. Berdasarkan literatur sejarah kopi, minuman ini sempat menjadi komoditas utama di dunia Islam. Minuman kopi sangat populer di kalangan peziarah Kota Mekah meskipun beberapa kali dinyatakan sebagai minuman terlarang. Para peziarah meminumnya untuk mengusir kantuk dan tetap terjaga saat beribadah malam. ")]),_c('br'),_c('h4',[_c('b',[_vm._v("2. Era Kekhalifahan dan Penyebaran Kopi ke Eropa")])]),_c('div',{staticClass:"text-center textgambar"},[_c('img',{staticClass:"rounded tulisandibawahgambar",staticStyle:{"width":"700px"},attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(7).jpg"),"alt":"kopi kerajaan"}}),_c('p',[_vm._v("Kopi diminum oleh bangsawan dan kelas menengah")])]),_c('br'),_c('p',[_vm._v(" Pada masa kekhalifahan Turki Utsmani di abad ke-15, kopi menjadi sajian utama di setiap perayaan. Melalui Turki inilah, minuman pahit berwarna hitam kecokelatan ini mulai dikenal dan disukai oleh orang-orang Eropa. Perbedaan budaya dan bahasa membuat bangsa Turki menyebut qahwah menjadi kahveh. Mulai dari sinilah kemudian orang-orang Belanda mengenal dan menyebutnya koffie. Orang-orang Kristen Eropa mengadopsi kebiasaan minum kopi karena erat kaitannya dengan kemegahan dan kekayaan orang-orang Turki Ustmani. Pada saat itu, kopi arabika merupakan primadona bahkan menjadi minuman kelas menengah di Inggris pada tahun 1600-an. ")]),_c('p',[_vm._v(" Kopi lantas menjadi komoditas penting di dunia. Orang-orang Eropa mencoba membudidayakannya sendiri. Namun, seringkali upaya tersebut gagal karena tanaman kopi tidak bisa tumbuh baik di sana. Oleh karena tidak bisa tumbuh baik di negerinya, beberapa negara di Eropa membawa tanaman ini ke daerah lain. Biasanya mereka memanfaatkan negara koloni atau jajahannya. ")]),_c('br'),_c('h3',[_c('b',[_vm._v("b. Legenda tentang Asal Usul Minuman Kopi")])]),_c('br'),_c('p',[_vm._v(" Ada dua dongeng legendaris tentang sejarah kopi. Legenda tersebut berkisah tentang Khaldi yang bertemu dengan kambing-kambing, dan Omar yang bekerja sebagai tabib. Dua mitos ini menceritakan awal manusia mulai mengonsumsi kopi. Kedua kisah tersebut sangat terkenal dan mendunia. Siapa saja yang mencoba untuk menelusuri sejarah kopi akan bertemu dengan dua mitos ini. ")]),_c('br'),_c('h4',[_c('b',[_vm._v("1. Khaldi dan Kambing yang Menari")])]),_c('br'),_c('br'),_c('div',{staticClass:"text-center textgambar"},[_c('img',{staticClass:"rounded tulisandibawahgambar",staticStyle:{"width":"700px"},attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(8).jpg"),"alt":"legenda kopi",",":""}}),_c('b',[_c('p',[_vm._v("Khaldi dan kambing yang menari")])])]),_c('p',[_vm._v(" Cerita ini merupakan mitos yang lahir di Ethiopia. Seiring dengan persebaran kopi di dunia, kisah ini pun tersebar secara lisan hingga melegenda. Konon, hiduplah seorang lelaki penggembala kambing bernama Khaldi pada kisaran tahun 850. Suatu hari, kambing-kambingnya melompat-lompat kegirangan seperti sedang menari. Usut punya usut, ia mendapati kambingnya telah memakan buah beri merah dari pohon yang asing. Penasaran dengan yang dialami kambingnya, Khaldi mencoba buah tersebut. Setelah memakannya, ia menjadi bersemangat seperti kambing-kambingnya itu. Khaldi menceritakan apa yang dialaminya kepada petapa atau biarawan. Si biarawan kemudian tertarik untuk mencoba buah ajaib itu. Ia pun bisa menjadi lebih kuat dan terjaga sepanjang malam tanpa mengantuk untuk berdoa. Oleh karena buah tersebut terasa sangat pahit, maka biarawan itu mulai mengolahnya. Ia mencoba memanggang dan menyeduh buah tersebut. Sejak itulah kopi mulai dikenal sebagai minuman yang dapat menambah tenaga dan mengusir rasa kantuk. ")]),_c('br'),_c('br'),_c('h4',[_c('b',[_vm._v("2. Omar Si Tabib Sufi")])]),_c('div',{staticClass:"text-center textgambar"},[_c('img',{staticClass:"rounded tulisandibawahgambar",staticStyle:{"width":"700px"},attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(9).jpg"),"alt":"legenda kopi",",":""}}),_c('b',[_c('p',[_vm._v("Omar Si Tabib Sufi")])])]),_c('br'),_c('p',[_vm._v(" Pada suatu hari, hidup seorang tabib penganut sufi di kota Mocha, Yaman. Ia bernama Ali bin Omar al Shadili dan biasa disapa Omar. Omar dikenal sebagai tabib yang memadukan tindakan medis dengan doa. Hampir segala penyakit bisa ia sembuhkan dengan cara itu. Ia pun menjadi tabib terkenal dan terpercaya di kota Mocha. Kepopuleran Omar tersebut tidak disukai oleh penguasa lokal. Segala upaya dilakukan untuk menjatuhkan Omar, seperti menggosipkannya telah bersekutu dengan setan untuk menyembuhkan pasiennya. Akhirnya masyarakat mengusir Omar dari Mocha. Omar pergi menjauh dan tinggal di dalam gua di luar kota Mocha. Pada saat ia mulai kelaparan, ia menemukan semak yang penuh dengan buah beri berwarna merah. "),_c('br'),_c('br'),_vm._v("Omar berpikir bahwa buah tersebut merupakan tanda Tuhan hendak menyelamatkannya. Ia pun memakan buah itu untuk mengusir rasa laparnya. Oleh karena rasa beri merah itu pahit, segala cara dilakukannya untuk mengolah buah itu hingga ke bijinya. Usaha Omar tidak membuahkan hasil karena ia tetap tak bisa menikmati buah itu. Ia pun hanya meminum cairan dari biji buah itu untuk memuaskan rasa hausnya. Tidak disangka, cairan yang ia minum mampu menyegarkan tubuhnya. Singkat cerita, banyak pasien datang ke gua dan meminta Omar untuk kembali menyembuhkan penyakit orang-orang. Omar pun mulai menggunakan air seduhan dari biji buah beri itu sebagai obat mujarab. Air itu pun terkenal dan disebut dengan nama Mocha. ")]),_c('br'),_c('h3',[_c('b',[_vm._v("c. Sejarah Kopi Di Indinesia")])]),_c('br'),_c('p',[_vm._v(" Awalmulanya, Sejarah kopi di Indonesia dimulai pada tahun 1696 ketika Belanda membawa kopi dari Malabar, India, ke Jawa. Mereka membudidayakan tanaman kopi tersebut di Kedawung, sebuah perkebunan yang terletak dekat Batavia. Namun upaya ini gagal kerena tanaman tersebut rusak oleh gempa bumi dan banjir. Upaya kedua dilakukan pada tahun 1699 dengan mendatangkan stek pohon kopi dari Malabar. "),_c('br'),_vm._v("Pada tahun 1706 sampel kopi yang dihasilkan dari tanaman di Jawa dikirim ke negeri Belanda untuk diteliti di Kebun Raya Amsterdam. Hasilnya sukses besar, kopi yang dihasilkan memiliki kualitas yang sangat baik. Selanjutnya tanaman kopi ini dijadikan bibit bagi seluruh perkebunan yang dikembangkan di Indonesia. ")]),_c('br'),_c('img',{staticClass:"rounded float-left spasigambar",attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(2).jpg"),"alt":"Sejarah kopi"}}),_c('p',[_vm._v(" Belanda pun memperluas areal budidaya kopi ke Sumatera, Sulawesi, Bali, Timor dan pulau-pulau lainnya di Indonesia. Pada tahun 1878 terjadi tragedi yang memilukan. Hampir seluruh perkebunan kopi yang ada di Indonesia terutama di dataran rendah rusak terserang penyakit karat daun atau Hemileia vastatrix (HV). Kala itu semua tanaman kopi yang ada di Indonesia merupakan jenis Arabika (Coffea arabica). Untuk menanggulanginya, Belanda mendatangkan spesies kopi liberika (Coffea liberica) yang diperkirakan lebih tahan terhadap penyakit karat daun. "),_c('br'),_vm._v("Sampai beberapa tahun lamanya, kopi liberika menggantikan kopi arabika di perkebunan dataranrendah.Di pasar Eropa kopi liberika saat itu dihargai sama dengan arabika. Namun rupanya tanaman kopi liberika juga mengalami hal yang sama, rusak terserang karat daun. Kemudian pada tahun 1907 Belanda mendatangkan spesies lain yakni kopi robusta (Coffea canephora). Usaha kali ini berhasil, hingga saat ini perkebunan-perkebunan kopi robusta yang ada di dataran rendah bisa bertahan. Pasca kemerdekaan Indonesia tahun 1945, seluruh perkebunan kopi Belanda yang ada di Indonesia di nasionalisasi. Sejak itu Belanda tidak lagi menjadi pemasok kopi dunia. ")]),_c('br'),_c('p',[_vm._v(" Pascakemerdekaan, setelah pemerintah Hindia Belanda meninggalkan Indonesia, laju perkebunan kopi pun sedikit terhambat. Namun, berkat komitmen serta kegigihan para petani dan nasionalisasi perkebunan eks pemerintahan Hindia Belanda, akhirnya perkebunan kopi lambat laun mulai bangkit dan berkembang. ")]),_c('br'),_c('p',[_vm._v(" Setidaknya ada satu novel karya Douwes Dekker berjudul Max Havelaar yang membantu mengubah opini masyarakat tentang Sistem Tanam Paksa. Novel tersebut berkisah tentang seorang pedagang kopi dan sekaligus kritik terhadap kesewenang-wenangan pemerintahan Hindia Belanda terhadap rakyat. Oleh karena peran novel itu, maka ada salah satu produk coffee blend dari Indonesia yang menggunakan kata Havelaar sebagai nama produknya. Tahun 2000-an, kopi Indonesia kembali melejit. Indonesia masuk dalam negara penghasil kopi terbesar keempat di dunia setelah Brazil, Vietnam, dan Kolombia. Keanekaragaman cita rasa kopi yang tumbuh di berbagai daerah di Indonesia diakui oleh mancanegara. ")]),_c('br'),_c('h3',[_c('b',[_vm._v("Bukan Sekadar Minuman, Melainkan Kisah Peminumnya")])]),_c('br'),_c('p',[_vm._v(" Hampir di semua tempat, orang-orang menghadirkan kopi sebagai pelengkap beraktivitas. Mulai dari bangun pagi, di tengah-tengah pekerjaan, hingga pada saat bercakap hangat dengan kawan atau kolega. Bisa di rumah, di kantor, maupun gang-gang sempit di sudut kota. Minuman legendaris ini bukan sekadar sebagai penghilang rasa dahaga saja, melainkan berisi kisah para peminumnya. Setiap orang memberikan makna tersendiri pada kopi yang ada di cangkirnya. Demikian pula cara menikmatinya yang tentu berbeda-beda. Mungkin orang zaman dahulu tak menyangka tanaman misterius ini bisa menjelma jadi minuman yang populer sejagat raya. Oleh karenanya, sangat baik bagi kita untuk mengetahui sejarah kopi untuk lebih mengenal dan memahami minuman kita ")]),_c('p',{staticClass:"comments text-center"},[_vm._v("Berikan Komentar Anda!")]),_vm._v("======= "),_c('p',[_vm._v(" Pada suatu hari, hidup seorang tabib penganut sufi di kota Mocha, Yaman. Ia bernama Ali bin Omar al Shadili dan biasa disapa Omar. Omar dikenal sebagai tabib yang memadukan tindakan medis dengan doa. Hampir segala penyakit bisa ia sembuhkan dengan cara itu. Ia pun menjadi tabib terkenal dan terpercaya di kota Mocha. Kepopuleran Omar tersebut tidak disukai oleh penguasa lokal. Segala upaya dilakukan untuk menjatuhkan Omar, seperti menggosipkannya telah bersekutu dengan setan untuk menyembuhkan pasiennya. Akhirnya masyarakat mengusir Omar dari Mocha. Omar pergi menjauh dan tinggal di dalam gua di luar kota Mocha. Pada saat ia mulai kelaparan, ia menemukan semak yang penuh dengan buah beri berwarna merah. "),_c('br'),_c('br'),_vm._v("Omar berpikir bahwa buah tersebut merupakan tanda Tuhan hendak menyelamatkannya. Ia pun memakan buah itu untuk mengusir rasa laparnya. Oleh karena rasa beri merah itu pahit, segala cara dilakukannya untuk mengolah buah itu hingga ke bijinya. Usaha Omar tidak membuahkan hasil karena ia tetap tak bisa menikmati buah itu. Ia pun hanya meminum cairan dari biji buah itu untuk memuaskan rasa hausnya. Tidak disangka, cairan yang ia minum mampu menyegarkan tubuhnya. Singkat cerita, banyak pasien datang ke gua dan meminta Omar untuk kembali menyembuhkan penyakit orang-orang. Omar pun mulai menggunakan air seduhan dari biji buah beri itu sebagai obat mujarab. Air itu pun terkenal dan disebut dengan nama Mocha. ")]),_c('div',{staticClass:"text-center textgambar"},[_c('p',[_c('b',[_vm._v("Source Artikel :")]),_c('a',{attrs:{"href":"https://www.sasamecoffee.com/kopipedia/sejarah-dan-jenis-kopi/"}},[_vm._v(" Klik Disini! ")])])]),_c('br'),_c('h3',[_c('b',[_vm._v("c. Sejarah Kopi Di Indinesia")])]),_c('br'),_c('p',[_vm._v(" Awalmulanya, Sejarah kopi di Indonesia dimulai pada tahun 1696 ketika Belanda membawa kopi dari Malabar, India, ke Jawa. Mereka membudidayakan tanaman kopi tersebut di Kedawung, sebuah perkebunan yang terletak dekat Batavia. Namun upaya ini gagal kerena tanaman tersebut rusak oleh gempa bumi dan banjir. Upaya kedua dilakukan pada tahun 1699 dengan mendatangkan stek pohon kopi dari Malabar. "),_c('br'),_vm._v("Pada tahun 1706 sampel kopi yang dihasilkan dari tanaman di Jawa dikirim ke negeri Belanda untuk diteliti di Kebun Raya Amsterdam. Hasilnya sukses besar, kopi yang dihasilkan memiliki kualitas yang sangat baik. Selanjutnya tanaman kopi ini dijadikan bibit bagi seluruh perkebunan yang dikembangkan di Indonesia. ")]),_c('br'),_c('img',{staticClass:"rounded float-left spasigambar",staticStyle:{"height":"300px","margin-right":"20px"},attrs:{"src":require("../../assets/img/Sejarah/Sejarah-Kopi(2).jpg"),"alt":"Sejarah kopi"}}),_c('p',[_vm._v(" Belanda pun memperluas areal budidaya kopi ke Sumatera, Sulawesi, Bali, Timor dan pulau-pulau lainnya di Indonesia. Pada tahun 1878 terjadi tragedi yang memilukan. Hampir seluruh perkebunan kopi yang ada di Indonesia terutama di dataran rendah rusak terserang penyakit karat daun atau Hemileia vastatrix (HV). Kala itu semua tanaman kopi yang ada di Indonesia merupakan jenis Arabika (Coffea arabica). Untuk menanggulanginya, Belanda mendatangkan spesies kopi liberika (Coffea liberica) yang diperkirakan lebih tahan terhadap penyakit karat daun. "),_c('br'),_vm._v("Sampai beberapa tahun lamanya, kopi liberika menggantikan kopi arabika di perkebunan dataranrendah.Di pasar Eropa kopi liberika saat itu dihargai sama dengan arabika. Namun rupanya tanaman kopi liberika juga mengalami hal yang sama, rusak terserang karat daun. Kemudian pada tahun 1907 Belanda mendatangkan spesies lain yakni kopi robusta (Coffea canephora). Usaha kali ini berhasil, hingga saat ini perkebunan-perkebunan kopi robusta yang ada di dataran rendah bisa bertahan. Pasca kemerdekaan Indonesia tahun 1945, seluruh perkebunan kopi Belanda yang ada di Indonesia di nasionalisasi. Sejak itu Belanda tidak lagi menjadi pemasok kopi dunia. ")]),_c('br'),_c('p',[_vm._v(" Pascakemerdekaan, setelah pemerintah Hindia Belanda meninggalkan Indonesia, laju perkebunan kopi pun sedikit terhambat. Namun,