agat raya. Oleh karenanya, sangat baik bagi kita untuk mengetahui sejarah kopi untuk lebih mengenal dan memahami minuman kita. ")]),_c('br'),_c('p',[_vm._v(" Nah sekarang kita tahu kan bagaimana sejarah dan perkembangan kopi di dunia dan di Indonesia sendiri, sebagai penikmat kopi kita wajib tau tentang berbagai jenis kopi yang ada di sekitar kita umumnya, nah untuk mempelajari lebih lanjut tentang bagaimana proses kopi dari biji sampai bisa dikonsumsi, dan berbagai macam hal tentang metode pembuatan kopi serta istilah - istilah yang bahkan belum pernah kita dengar bisa kita pelajari dengan lebih mendalam, yuk jangan pernah lelah untuk belajar dan menambah ilmu. "),_c('br'),_c('br'),_c('i',[_vm._v("By Author")])]),_c('img'),_c('p',{staticClass:"comments text-center"},[_vm._v("Berikan Komentar Anda!")]),_c('div',{attrs:{"id":"disqus_thread"}})])])])])])])])])}]

